import React, { Component } from 'react';
import { observer } from 'mobx-react';

@observer
export default class Badge extends Component {
  render() {
    return (
      <div className="ui label about-badge">
        {this.props.text}
        <div className="detail">
          {this.props.info}
        </div>
      </div>
    );
  }
}



