import React, { Component } from 'react';
import { observer } from 'mobx-react';
import logo from './asset/logo.png';
import Badge from './component/Badge'
import CommandLink from './component/CommandLink'

import './App.css';

@observer
export default class App extends Component {
  render() {
    const vm = this.props.viewModel;
    return (
      <div className="ui card">
        <div className="image">
          <img src={logo} alt=""></img>
        </div>
        <div className="content">
          <a className="header">Neutronium</a>
          <div className="meta">
            <span className="date">Copyright (c) 2018 David Desmaisons</span>
          </div>
          <div className="description labels">
            <Badge text="Neutronium.Core" info={vm.CoreVersion}></Badge>
            <Badge text="Neutronium.WPF" info={vm.WPFVersion}></Badge>
            <Badge text={vm.WebBrowser} info={vm.WebBrowserVersion}></Badge>
            {(vm.WebEnvironment) &&
              <Badge text="Platform" info={vm.WebEnvironment}></Badge>
            }
            <Badge text={vm.JavascriptFramework} info={vm.JavascriptFrameworkVersion}></Badge>
            <Badge text="Browser" info={vm.BrowserBinding}></Badge>
            <Badge text="Binding" info={vm.MVVMBinding}></Badge>
          </div>
        </div>
        <div className="extra content">
          <CommandLink class="right floated" command={vm.GoToGithub} text="See on Github" icon="github"></CommandLink>
        </div>
      </div>
    );
  }
}



